import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import { io } from "socket.io-client";

let socket = io();

function getTime() {
  let date = new Date();
  let hours = ("0" + date.getHours()).substr(-2, 2);
  let minutes = ("0" + date.getMinutes()).substr(-2, 2);
  return hours + ":" + minutes;
}

const Chat = () => {
  const [username, setUsername] = useState('');
  const [allMessages, setAllMessages] = useState([]);

  useEffect(() => {
    document.getElementById("chat")?.scrollTo(0, document.getElementById("chat").scrollHeight);
    console.log('object asdasd');
  }, [allMessages])

  socket.on('chat message', (messages) => {
    setAllMessages(messages);
  });

  const handleCallbackMessage = (FormData) => {
    if (FormData) {
      socket.emit('chat message', FormData, username, socket.id, getTime());
    }
  };

  let message;
  if (allMessages.length) {
    message = (
      <MessageList
        allMessages={allMessages}
        userId={socket.id}
      />
    );
  }

  return (
    username ?
      <div className="wrapper">
        < div className="contacts" >
          <Contact />
        </div >
        <div className="chat" id="chat">
          {message}
        </div>
        <Form parentCallback={handleCallbackMessage} />
      </div >
      : <Auth setUsername={setUsername} />
  );

}

class Form extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };
    this.handleChange = this.handleChange.bind(this);
    this.onTrigger = this.onTrigger.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
    event.preventDefault();
  }

  handleEnter(event) {
    if (event.code === 'Enter' && event.ctrlKey) {
      event.preventDefault();
      this.props.parentCallback(event.target.value.trim(/(\s|\n|\r)/));
      this.setState({ value: "" });
    }
  }

  onTrigger = (event) => {
    this.props.parentCallback(this.state.value);
    this.setState({ value: "" });
    event.preventDefault();
  };

  render() {
    return (
      <form id="form" onSubmit={this.onTrigger}>
        <textarea
          id="input"
          placeholder="Your message"
          autoComplete="off"
          type="text"
          value={this.state.value}
          onChange={this.handleChange}
          onKeyUp={(e) => this.handleEnter(e)}
        />
        <button id="sendButton"></button>
      </form>
    );
  }
}

const Contact = () => {
  const [contacts, setContacts] = useState([]);

  socket.on('user join', function (users) {
    setContacts(users);
  });

  return <PeopleList contacts={contacts} />;
}

function PeopleList({ contacts }) {
  const contactsList = contacts.map(([user, , time]) => (
    //key especificada dentro del array.
    <People key={user} value={user} time={time} />
  ));

  return <ul id="Contacts-List"> {contactsList} </ul>;
}

function People(props) {
  const firstLetter = props.value.trim(' ')[0].toUpperCase();

  return (
    <li className="people">
      <div className="PeopleImage">{firstLetter}</div>
      <div className="PeopleInfo">
        <span>
          <p className="PeopleName"> {props.value}</p>
          <p className="People-Date"> {props.time} </p>
        </span>
      </div>
    </li>
  );
}

function MessageList({ allMessages, userId }) {
  const messagesList = allMessages.map(([message, user, id, time]) => (
    //key especificada dentro del array.
    <Message key={message} value={message} activePeople={user} messageId={id} userId={userId} time={time} />
  ));

  return <ul id="messages"> {messagesList} </ul>;
}

function Message(props) {
  let msgOwner;

  const firstLetter = props.activePeople[0].trim(' ')[0].toUpperCase();

  msgOwner = props.messageId === props.userId ? 'Me' : 'Other';
  return (
    <li className={msgOwner}>
      {msgOwner === 'Other' && < div className="PeopleImage">{firstLetter}</div>}
      <span>
        <p className="Message-User item">{props.activePeople}</p>
        <p className="Text item">{props.value}</p>
        <p className="Message-Date">{props.time}</p>
      </span>
    </li >
  );
}

const Auth = ({ setUsername }) => {
  const [name, setName] = useState('');

  const handleChange = (e) => {
    e.preventDefault();
    setName(e.target.value);
    document.getElementById('LoginError').className = "Hide";
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if (name.length < 1 || name.length > 10) {
      document.getElementById('LoginError').className = "Show";
    } else {
      sessionStorage.setItem('username', name);
      socket.emit('user join', name, getTime());
      setUsername(name);
    }
  }

  return (
    <div className="Login">
      <h1>Guava Chat</h1>
      <form onSubmit={handleSubmit} className="LoginForm">
        <input
          className="LoginInput"
          type='text'
          placeholder="Your username"
          autoComplete="off"
          onChange={handleChange}
          value={name}
        />
        <button className="LoginSubmit" type="submit">Login</button>
      </form>
      <span id="LoginError" className="Hide">*Username must be between one and ten characters</span>
    </div>
  )
}

ReactDOM.render(
  <Chat />,
  document.getElementById("root"));
